import {Directive, TemplateRef, Input, OnInit} from '@angular/core';

@Directive({
  selector: '[adaptTreeNodeTemplate]'
})
export class AdaptTreeNodeTemplateDirective implements OnInit {

  @Input('adaptTreeNodeTemplate') name: string;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  constructor(public template: TemplateRef<any>) {}

  ngOnInit(): void {
    this.name = this.name || 'default';
  }
}
